<template>
  <div>
    <h1 class="mt-3">Abwesenheiten</h1>
    <table class="table">
      <thead>
        <tr>
          <th>Startdatum</th>
          <th>Enddatum</th>
          <th>Grund</th>
          <th>Vorname</th>
          <th>Nachname</th>
        </tr>
      </thead>
      <tbody>
        <tr :key="index" v-for="(index,i) in 100">
          <td>
            {{ absences[i].startdate }}
          </td>
          <td>
            {{ absences[i].enddate }}
          </td>
          <td>
            {{ absences[i].reason }}
          </td>
          <td>
            {{ persons[i].firstname }}
          </td>
          <td>
            {{ persons[i].lastname }}
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data: function () {
    return {
      absences: [],
      persons: [],
    };
  },
  mounted: function() {
      this.getAbsences();
      this.getPersons();
  },
    
  methods: {
      getAbsences: function() {
          let url = "http://localhost:8080/shiftplanner/absences"
          axios.get(url).then(response => {
              this.absences = response.data;
              this.absences.sort((a,b) => new Date(a.startdate).getTime() - new Date (b.startdate).getTime());
          })
      },
      getPersons: function() {
          let url = "http://localhost:8080/shiftplanner/persons"
          axios.get(url).then(response => {
              this.persons = response.data;
          })
      },
  }
};
</script>